import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Default',
      redirect: '/home',
      component: Home
    },
    {
      // home页面并不需要被访问，只是作为其它组件的父组件
      path: '/home',
      name: 'Home',
      component: Home,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: () => import('../components/home/AppIndex')
        },
        {
          path: '/test',
          name: 'Test',
          component: () => import('../components/test/Tests')
        },
        {
          path: '/test/testDetails',
          name: 'TestDetails',
          component: () => import('../components/test/TestDetails')
        },
        // { // 暂时将测试详情页单独放出来，方便测试
        //   path: '/testdetails',
        //   name: 'TestDetails',
        //   component: () => import('../components/test/TestDetails')
        // },
        {
          path: '/library',
          name: 'Library',
          component: () => import('../components/library/ScenarioIndex')
        },
        {
          path: '/library/scenarios',
          name: 'ScenarioIntro',
          component: () => import('../components/library/ScenarioIntro')
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('../components/Login')
    },
    {
      path: '/register',
      name: 'Register',
      component: () => import('../components/Register')
    },
    {
      path: '*',
      component: () => import('../components/pages/Error404')
    }
  ]
})

// 用于创建默认路由
export const createRouter = routes => new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Default',
      redirect: '/home',
      component: Home
    },
    {
      // home页面并不需要被访问，只是作为其它组件的父组件
      path: '/home',
      name: 'Home',
      component: Home,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: () => import('../components/home/AppIndex')
        },
        {
          path: '/test',
          name: 'Test',
          component: () => import('../components/test/Tests')
        },
        {
          path: '/test/testDetails',
          name: 'TestDetails',
          component: () => import('../components/test/TestDetails')
        },
        // { // 暂时将测试详情页单独放出来，方便测试
        //   path: '/testdetails',
        //   name: 'TestDetails',
        //   component: () => import('../components/test/TestDetails')
        // },
        {
          path: '/library',
          name: 'Library',
          component: () => import('../components/library/ScenarioIndex')
        },
        {
          path: '/library/scenarios',
          name: 'ScenarioIntro',
          component: () => import('../components/library/ScenarioIntro')
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('../components/Login')
    },
    {
      path: '/register',
      name: 'Register',
      component: () => import('../components/Register')
    },
    {
      path: '*',
      component: () => import('../components/pages/Error404')
    }
  ]
})



